import dayjs from "dayjs";

export function initBrandOptions(): ElementReact.SelectOptionProps[] {
    return [
        {label: 'Guangzhou Shirui', value: 'guangzhou_shirui'},
        {label: 'A', value: 'a'},
        {label: 'B', value: 'b'}
    ]
}

export function initCodeOptions(): ElementReact.SelectOptionProps[] {
    return [
        {label: 'All', value: 'all'},
        {label: 'A', value: 'a'},
        {label: 'B', value: 'b'}
    ]
}

export function init30DaysRangeDateType(): ElementReactLibs.dateType[] {
    const today = dayjs();
    return [today.subtract(1, 'month').toDate(), today.toDate()];
}
